.popular {
    position: relative;
    padding-top: 110px;
    padding-bottom: 200px;

    @include mobile {
        padding-top: 65px;
        padding-bottom: 70px;
    }

    &__bg {
        position: absolute;
        top: -140px;
        left: 0;
        z-index: -1;
        height: 1150px;
        object-fit: cover;
        width: 100%;

        @include minitablet {
            top: -80px;
            height: 1000px;
        }

        @include mobile {
            display: none;
        }
    }

    &__bg-mob {
        display: none;

        @include mobile {
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: -1;
        }
    }

    &__title {
        color: #fff;
        max-width: 500px;
        margin-bottom: 10px;
    }

    &__subtitle {
        color: #fff;
        margin-bottom: 50px;
    }

    &__box {
        position: relative;

        
    }

    &__slider {
        @media (max-width: 600px) {
            max-width: 274px;
        }
    }

    &__switch {
        position: absolute;
        top: 50%;
        z-index: 3;
        background-color: #F1DC9A;
        width: 30px;
        height: 30px;
        padding-top: 3.5px;
        transform: translateY(-50%);
        
        svg {
            fill: transparent;
            stroke: #000000;
            width: 18px;
            height: 30px;
        }

        &--prev {
            left: -50px;

            @media (max-width: 600px) {
                left: 0;
            }
        }

        &--next {
            right: -50px;

            @media (max-width: 600px) {
                right: 0;
            }
        }

        
    }

    .swiper-slide {
        text-align: center;

        
    }

    &__img {
        max-width: 100%;
        width: 100%;
        
    }
}

